import React from 'react';
import ReactLoading from 'react-loading';
import {Colors} from "../Common/Constants";

export const LoaderTypes = {
  blank: 'blank',
  balls: 'balls',
  bars: 'bars',
  bubbles: 'bubbles',
  cubes: 'cubes',
  cylon: 'cylon',
  spin: 'spin',
  spinningBubbles: 'spinningBubbles',
  spokes: 'spokes',
}

export const Loader = () => {
  return (
    <ReactLoading type={LoaderTypes.bubbles} color={Colors.White} height={300} width={300} />
  )
}
