import React, {useState} from 'react';
import {Colors} from "../../Common/Constants";
import {shadow} from "../../Common/Styles";
import './button.css';

const commonStyles = {
  marginRight: 10,
  minWidth: 100,
  minHeight: 40,
  zIndex: 100,
  backgroundColor: Colors.White,
  borderRadius: 10,
  paddingTop: 3,
  paddingBottom: 3,
  paddingLeft: 13,
  paddingRight: 13,
  alignItems: 'center',
  textAlign: 'center',
  justifyContent: 'center',
}

const hoverStyles = {
  backgroundColor: Colors.Hover,
}

const Button = ({label, onClick, style = {}}) => {
  const [hover, setHover] = useState(false);

  const mouseOver = (event) => {
    setHover(true);
  }

  const mouseOut = (event) => {
    setHover(false);
  }

  return (
      <div role="button" tabIndex={0}  className={'hover-button'} onMouseOver={mouseOver} onMouseOut={mouseOut}
           style={{...commonStyles, ...style, ...shadow, ...(hover ? hoverStyles : {})}}
           onClick={onClick}
      >
        <p>
          {label || 'label here'}
        </p>
      </div>
  );
}


export default Button;
