import React from "react";

const PopUp = ({children}) => {
  return (
    <div style={{
        flex: 1,
        position: 'fixed',
        'z-index': 1,
        left: 'calc(100% + 5px)',
        width: '100%',
        top: '50%',
        transform: 'translate(0, -50%)'
    }}>
      {children}
    </div>
  )
}

export default PopUp;
