import React, { useState } from "react";
import Checkbox from "../Checkbox/Checkbox";
import { CheckboxState } from "../Tree/Tree";
import styles from "./checkboxlist.module.scss";

// export type Item = {
//   id: number;
//   name: string;
//   parentId: number;
// };

// type CheckboxListProps = {
//   items: Item[];
//   idsToRender?: number[];
//   indentLevel?: number;
//   onClick?: (id: number) => void;
//   getStateForId: (id: number) => CheckboxState;
// };

const CheckboxList = ({
  items,
  getStateForId,
  idsToRender = [],
  indentLevel = 0,
  onClick = () => {},
}) => {
  if (!idsToRender.length) {
    idsToRender = items.filter((i) => !i.parentId).map((i) => i.id);
  }

  const [states, updateStates] = useState(new Array(idsToRender.length).fill(false));

  // const [isActive, setIsActive] = useState(false);

  const getChildNodes = (parentId) => {
    const nodeItems = items.filter((i) => i.parentId === parentId);

    if (!nodeItems.length) return null;
    return (
      <CheckboxList
        items={items}
        idsToRender={nodeItems.map((i) => i.id)}
        indentLevel={indentLevel + 1}
        onClick={onClick}
        getStateForId={getStateForId}
      />
    );
  };

  return (
    <ul className={styles.list} style={{ paddingLeft: indentLevel * 20 }}>
      {idsToRender.map((id, index) => {
        const item = items.find((i) => i.id === id);
        const checkboxState = getStateForId(id);
        return (
          <React.Fragment key={item.id}>
            <li onClick={() => updateStates((prevState) => {
              const newState = [...prevState];
              newState[index] = !newState[index]
              return newState;
              })}>
              <Checkbox
                onClick={(e) => {e.stopPropagation(); onClick(item.id)}}
                isChecked={checkboxState === CheckboxState.CHECKED}
                isIndeterminate={checkboxState === CheckboxState.INDETERMINATE}
              />
              {item.name}
            </li>
            {states[index] && getChildNodes(item.id)}
          </React.Fragment>
        );
      })}
    </ul>
  );
};

export default CheckboxList;
