import React from "react";
import {MarkerType} from "react-flow-renderer";
import {Colors, defaultStrokeWidth} from "../Common/Constants";
import NodeContainer from "../Components/NodeContainer";
import DetailContainer from "../Components/DetailContainer";

export const extractNode = (vertex) => {
  // Input:
  // {
  //   "name": "0xe3549f2ca84ddc8b07dd5d81a9931c35d9f6c25d",
  //   "class": "unknown"
  // }
  const horizontalScaleByUID = 60;
  const verticalScaleByUID = 70;
  const uid = vertex.uid;

  return {
    id: uid.toString(),
    data: {
      container: (
        <NodeContainer text={vertex.name.slice(0, 15) + ' . . .'}/>
      ),
      detailContainer:  (
        <DetailContainer text={"Some details"}/>
      ),
    },
    type: "hiddenNode",
  }
}


export const convertToEdge = (edgeFromAPI) => {
  console.log(edgeFromAPI);

  // Input:
  // {
  //   {
  //     "uid": 0,
  //     "gas_value": 0.012861840061104128,
  //     "method_name": "swap",
  //     "currency": "-",
  //     "vertex_from": {
  //     "uid": 0,
  //       "name": "0xe3549f2ca84ddc8b07dd5d81a9931c35d9f6c25d",
  //       "annotation": null
  //   },
  //     "vertex_to": {
  //     "uid": 1,
  //       "name": "0x881d40237659c251811cec9c364ef91dc08d300c",
  //       "annotation": "Swap Router, MetaMask"
  //   }
  // }

  // Output:
  // {id: uuidv4(), source: '2', target: '3'},
  const edgeFromUid = edgeFromAPI.vertex_from.uid.toString();
  const edgeToUid = edgeFromAPI.vertex_to.uid.toString();

  return {
    id: edgeFromAPI.uid,
    type: 'input',
    source: edgeFromUid,
    target: edgeToUid,
    markerEnd: {
      type: MarkerType.ArrowClosed,
    },
    animated: true,
    style: {
      strokeWidth: defaultStrokeWidth,
      color: Colors.DefaultEdgeColor,
    }
  }
}
