import React, {useState} from 'react';
import {Handle} from "react-flow-renderer";
import PopUp from "./NodePopup";
import {Colors} from "../../Common/Constants";

const HiddenNode = ({data}) => {
  const [isVisible, setVisible] = useState(false);

  return (
    <div style={{flex: 1, minWidth: 150}} onDoubleClick={() => setVisible(!isVisible)}>
      <Handle type="target" position="top"/>
      <div style={{flex: 1, flexDirection: 'row'}}>
        <p style={{position: 'absolute', right: 10, top: 6, fontSize: 6, color: Colors.TextLightGray}}>Press twice</p>
        {data.container}
        {isVisible ? (
            <PopUp toggle={() => setVisible(!isVisible)}>
              {data.detailContainer}
            </PopUp>
          ) : undefined}
      </div>
      <Handle type="source" position="bottom"/>
    </div>
  );
};
export default HiddenNode;
