/* eslint-disable */
import ReactFlow, {Background, Controls, MiniMap, useEdgesState, useNodesState} from 'react-flow-renderer';
import {useStore} from "effector-react";
import {
  $displayedEdges,
  $displayedNodes,
  $edges,
  $filters,
  $visibilitySettings,
  setDisplayedEdges,
  setDisplayedNodes,
  setVisibilitySettings
} from "../Common/Storage";
import HiddenNode from "../Components/HiddenNode/HiddenNode";
import {useEffect} from "react";
import Button from "../Components/Button";
import {Colors, steppedStrokeWidth} from "../Common/Constants";
import Tree from '../Components/Tree/Tree.jsx';

const onInit = (reactFlowInstance) => console.log('flow loaded:', reactFlowInstance);

const nodeTypes = {
  'hiddenNode': HiddenNode,
}

const MainScreen = () => {
  const allEdges = useStore($edges);
  const allFilters = useStore($filters);
  const displayedEdges = useStore($displayedEdges);
  const displayedVertexes = useStore($displayedNodes);
  const visibilitySettings = useStore($visibilitySettings);

  const [flowNodes, setNodes, onNodesChange] = useNodesState(displayedVertexes);
  const [flowEdges, setEdges, onEdgesChange] = useEdgesState(displayedEdges);

  useEffect(() => {
    return setDisplayedNodes.watch((vertexes) => {
      setNodes([...vertexes]);
    });
  }, [])

  useEffect(() => {
    return setDisplayedEdges.watch((edges) => {
      setEdges([...edges]);
    });
  }, [])

  const getChangedEdge = (edge, color, width, zIndex) => {
    const newEdge = {
      ...edge,
      style: {
        stroke: color,
        strokeWidth: width,
      },
      zIndex: zIndex,
    };
    newEdge.markerEnd = {
      ...edge.markerEnd,
      color: color,
    };
    return newEdge;
  }


  useEffect(() => {
    let timer = undefined;

    if (visibilitySettings.showSteps){
      let stepNumber = 0;
      let currentEdges = [...displayedEdges];

      timer = setInterval(() => {
        if (stepNumber < currentEdges.length) {
          currentEdges[stepNumber] = getChangedEdge(currentEdges[stepNumber], Colors.Red, steppedStrokeWidth, stepNumber + 1);
          setDisplayedEdges(currentEdges);
          stepNumber += 1;
        }
      }, 1.1 * 1000);
    }

    return () => {
      clearInterval(timer);
    };

  }, [visibilitySettings.showSteps])

  console.log('ffrefrferefrerfrefefr', $filters)

  return (
    <div className="main">
    <ReactFlow
      nodes={flowNodes}
      edges={flowEdges}
      onNodesChange={onNodesChange}
      onEdgesChange={onEdgesChange}
      nodesConnectable={false}
      nodeTypes={nodeTypes}
      onInit={onInit}
      fitView
      attributionPosition="top-right"
    >
      <div style={{position: 'absolute', bottom: 30, left: 60, display: 'flex', flexDirection: 'row'}}>
        <Button
          onClick={() => {
            const newEdges = [...allEdges].filter((edge) => Math.random() >= 0.5);
            setDisplayedEdges(newEdges);
          }}
          label={'Clear random edges'}
        />
        <Button
          onClick={() => {
            setVisibilitySettings({
              showSteps: !visibilitySettings.showSteps,
            });
          }}
          label={visibilitySettings.showSteps ? 'Pause' : 'Show steps'}
        />

        <Button
          onClick={() => setDisplayedEdges(allEdges)}
          label={'Clear'}
        />
      </div>

      {/* <MiniMap
        nodeStrokeColor={(n) => {
          if (n.style?.background) return n.style.background;
          if (n.type === 'input') return '#0041d0';
          if (n.type === 'output') return '#ff0072';
          if (n.type === 'default') return '#1a192b';

          return '#eee';
        }}
        nodeColor={(n) => {
          if (n.style?.background) return n.style.background;

          return '#fff';
        }}
        nodeBorderRadius={2}
      /> */}
      <Controls/>
      <Background color="#ffffff" gap={16}/>
    </ReactFlow>
    <Tree items={allFilters} />
    </div>
  );
};

export default MainScreen;