/* eslint-disable */
import {createEffect, createEvent, createStore} from "effector";
import {downloadVertexEdges} from '../Utils/Api';
import {nodes as initialNodes} from "../Screens/TestData";
import {convertToEdge, extractNode} from "../Utils/Converters";

export const loadEdgesVertexToStore = createEffect('download nodes').use(() =>
  downloadVertexEdges()
    .then(res => {
      console.log("Edges downloaded");
      return res;
    }).catch(() => {
      // alert('Unable to load nodes');
      return initialNodes;
    })
)


const setEdges = createEvent();
export const $edges = createStore([]).on(
  setEdges,(store, edges) => [...edges]
);

const setNodes = createEvent();
export const $nodes = createStore([]).on(
  setNodes, (store, vertexes) => [...vertexes]
);

const setFilters = createEvent();
export const $filters = createStore([]).on(
  setFilters, (store, filters) => [...filters]
);

loadEdgesVertexToStore.done.watch(({params, result}) => {
  console.log('EdgesVertexes received. Start handling...');
  const width = 300;
  let dx = -width;
  let dy = 0;

  const edges = [...result.edges.map(item => convertToEdge(item))];
  const nodes = result.vertexes.map((item, key) => {
    const node = extractNode(item);
    dx = (dx + width) % (2 * width);
    dy += key % 2;
    node.position = {
      x: dx + Math.random() * 100,
      y: dy * 100 + Math.random() * 50
    };
    return node;
  });
  const filtersData = [];
  const mainFilters = new Map();
  const setOfFilters = new Set();
  let id = 0;
  result.vertexes.map(item => item.class).forEach(element => {
    id++;
    if (!mainFilters.has(element.name)) {
      mainFilters.set(element.name, id);
      filtersData.push({id: id, name: element.name, parentId: 0})
      id++;
    }
    if (setOfFilters.has(element.value)) {
      return;
    }
    filtersData.push({id: id, name: element.value, parentId: mainFilters.get(element.name)})
    setOfFilters.add(element.value);
  });

  setNodes(nodes);
  setEdges(edges);
  setFilters(filtersData);

  setDisplayedEdges(edges);
  setDisplayedNodes(nodes);
})


export const setDisplayedEdges = createEvent();
export const $displayedEdges = createStore([]).on(
  // TODO    
  setDisplayedEdges,(store, edges) => {
    return [...edges];
  }
);

export const setDisplayedNodes = createEvent();
export const $displayedNodes = createStore([]).on(
  // TODO    
  setDisplayedNodes, (store, vertexes) => [...vertexes]
);

export const setVisibilitySettings = createEvent();
export const $visibilitySettings = createStore({
  showSteps: false
}).on(
    setVisibilitySettings, (store, settings) => {return {...store, ...settings}}
);



